body {
  background-color: rgba(149, 147, 147, .486);
}

.container {
  text-align: center;
  width: 80%;
  margin: 0 auto;
  padding: 16px;
}

.form-label {
  text-align: right;
  padding: 8px;
  font-family: Georgia, Times New Roman, Times, serif;
  font-size: 24px;
}

.input {
  width: 256px;
  height: 56px;
  text-align: center;
  background-color: rgba(165, 160, 153, .514);
  border: none;
  border-radius: 8px;
  margin-right: 16px;
  padding: 8px;
  font-family: Georgia, Times New Roman, Times, serif;
  font-size: 24px;
}

.button {
  width: 144px;
  height: 72px;
  border: none;
  border-radius: 28px;
  margin-bottom: 16px;
  font-size: 22px;
  animation-name: pop;
  animation-duration: 5s;
  animation-timing-function: linear;
  animation-iteration-count: infinite;
  overflow: hidden;
}

@keyframes pop {
  0% {
    background-color: rgba(224, 181, 124, .647);
  }

  100% {
    background-color: rgba(88, 82, 72, .576);
  }
}

.button:hover {
  background-color: rgba(147, 130, 107, .494);
  font-weight: 700;
}

.button-more {
  margin-top: 16px;
}

.articles {
  grid-gap: 20px;
  max-width: calc(100vw - 80px);
  grid-template-columns: repeat(auto-fill, minmax(16%, 1fr));
  margin: 0 auto 30px;
  padding: 0;
  list-style: none;
  display: grid;
}

.articles li {
  height: 450px;
  overflow: hidden;
}

.articles img {
  width: 100%;
  height: 200px;
  object-fit: cover;
  display: block;
}

.articles a {
  color: #2a2a2a;
  text-decoration: none;
}

